.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

#root > div > div.ui.bottom.attached.segment.active.tab > table > tbody{
  max-height: 200px;
}

#users-container{
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  max-height: 300px;
}

.CircularProgressbar {
  width: 100px !important;
}